@mixin one-cell {
  display: none;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  flex-basis: $cell-size;
  height: $cell-size;
  font-family: $date-font-family;
  font-size: $date-font-size;
  background: $cell-background;
  border: 1px solid black;
  margin-right: $cell-margin;
  margin-bottom: $cell-margin;
}
